function reducer (state, action){
    switch (action.type){
        case "":
            return {
                ...state
            }
        default: return state
    }
}

function News (){
    const [state, setState] = useReducer(reducer, {
        data:[], form:"", loading:true
    })
    return (
        <Layout header = {<Header/>}>

        </Layout>
    )
}

